import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ServiceHelperService } from 'src/app/components/helper/service/service-helper.service';
import { WhatsappService } from 'src/app/components/helper/whatsapp.service';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.css']
})
export class ServicesComponent extends ServiceHelperService {
  whatsAppUrl = '';
  constructor(private whatsAppService: WhatsappService, route: ActivatedRoute) {
    super(route);
  }

  ngOnInit(): void {
    this.whatsAppUrl = this.whatsAppService.getWhatsAppUrl();
  }
}
