import { Component, OnInit } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { ContactHelperService } from "src/app/components/helper/contact/contact-helper.service";
import { ContactForm } from "src/app/components/models/contact/ContactForm";
import { ContactResponse } from "src/app/components/models/contact/ContactResponse";
import { Contact } from "../../../models/contact/contact";

@Component({
  selector: "app-quote",
  templateUrl: "./quote.component.html",
  styleUrls: ["./quote.component.css"],
})
export class QuoteComponent implements OnInit {
  model = new Contact();
  submitted = false;
  isLoading = false;
  error: {} | undefined;
  dataResponse!: ContactResponse;
  profileForm!: FormGroup;
  disabled = false;
  constructor(private contactService: ContactHelperService) {
    this.profileForm = new FormGroup({
      name: new FormControl("", Validators.required),
      phone: new FormControl(""),
      email: new FormControl("", [Validators.required, Validators.email]),
      message: new FormControl("", Validators.required),
      honeypot: new FormControl(""),
    });
  }
  onSubmit() {
    if (
      this.profileForm.get("honeypot")?.value == null ||
      this.profileForm.get("honeypot")?.value == ""
    ) {
      this.isLoading = true;
      this.disabled = true;

      const contactFormNew: ContactForm = {
        name: this.profileForm.get("name")?.value,
        email: this.profileForm.get("email")?.value,
        phone: this.profileForm.get("phone")?.value,
        message: `Asunto: ${
          this.profileForm.get("subject")?.value
            ? this.profileForm.get("subject")?.value
            : "Contáctame"
        } Mensaje: ${this.profileForm.get("message")?.value}`,
      };

      return this.contactService.contactFormNew(contactFormNew).subscribe(
        (data: ContactResponse) => {
          this.dataResponse = data;
          if (this.dataResponse.ok === true) {
            this.submitted = true;
            this.profileForm.reset();
          } else {
            this.submitted = false;
          }
          this.disabled = false;
          this.isLoading = false;
        },
        (error) => {
          this.error = error;
          this.submitted = false;
          this.isLoading = false;
          this.disabled = false;
        }
      );
    } else {
      return;
    }
  }

  ngOnInit(): void {}
}
